@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;700&display=swap);
*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  overflow-x: hidden;
  margin: 0;
  font-family: Public Sans, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p {
  color: #9698a6;
  font-size: 20px;
}
h1{
  font-weight: 400;
  font-size: 60px;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
ul {
  margin: 0;
}
textarea:focus, input:focus, button {
  outline: none;
}
button {
  background: linear-gradient(to right,hsl(136, 65%, 51%), hsl(192, 70%, 51%));
  color: white;
  font-weight: 700;
  border: 0;
  border-radius: 50px;
  width: 150px;
  height: 40px;
  box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.25);
  cursor: pointer;
}
button:hover {
  background: linear-gradient(to right,hsl(136, 92%, 59%), hsl(192, 91%, 58%));
}
code {
  font-family: Public Sans, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Burger {
    display: none;
}
.burger__icon {
    height: 24px;
    width: 18px;
    cursor: pointer;
    position: relative;
}
.burger__icon>span {
    z-index: 1;
    height: 2px;
    width: 100%;
    display: block;
    position: absolute;
    top: 50%; transform: translateY(-50%);
    background-color: rgb(0, 0, 0);
    transition: all ease-in-out .3s;
}
.burger__icon>span::before, .burger__icon>span::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: rgb(0, 0, 0);
}
.burger__icon>span::before {
    transform: translateY(-5px);
    transition: all ease-in-out .3s;
}
.burger__icon>span::after {
    transform: translateY(5px);
    transition: all ease-in-out .3s;
}
.burger__icon.active>span{
    background-color: rgba(0, 0, 0, 0);
}

.burger__icon.active>span::before {
    transform: translateY(0px) rotateZ(45deg);
}
.burger__icon.active>span::after {
    transform: translateY(0px) rotateZ(-45deg);
}

.burger__anim {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: center;
    top: 0;right: 0;margin: 100px 0 0 0;
}
.burger__sidebar {
  position: absolute;
  box-shadow: 0px 20px 50px 0px rgba(0,0,0,0.25);
  border-radius: 20px;
  background:rgb(255, 255, 255);
  height: 20em;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 80em;
}

.burger__sidebar.open {
  margin-left: 80em;
  -webkit-animation: side-open .5s forwards;
          animation: side-open .5s forwards;
}
@-webkit-keyframes side-open {
    100% {margin-left: 0;}
}
@keyframes side-open {
    100% {margin-left: 0;}
}
.burger__sidebar.close {
  margin-left: 0;
  -webkit-animation: side-close .5s forwards;
          animation: side-close .5s forwards;
}
@-webkit-keyframes side-close {
    100% { margin-left: 80em;}
}
@keyframes side-close {
    100% { margin-left: 80em;}
}
.burger__sidebar__items {
    padding: 0;
    text-align: center;
}
.burger__sidebar__items>li {
    margin: 30px 0;
}
.burger__sidebar__items>li>a {
    color: black;
}
@media screen and (max-width: 650px) {
    .Burger {
        display: block;
    }
}
.Header {
    z-index: 10;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    position: fixed;
    width: 100%;
    align-items: center;
    height: 80px;
    background: rgb(255, 255, 255);
    box-shadow: 0px 20px 50px 0px rgba(0,0,0,0.1);
}
.header__logo {
    width: 139px;
    height: 20px;
}
.header__navbar {
    display: flex;
}

.header__navbar>li {
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin: 0 30px;
    height: 80px;
}

.header__navbar>li::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    opacity: 0;
    background: linear-gradient(to right,hsl(136, 65%, 51%), hsl(192, 70%, 51%));
    transition: all ease-in-out .1s;
}

.header__navbar>li:hover::after {
    opacity: 1;
    transition: all ease-in-out .1s;
}

.header__navbar>li>a {
    padding: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    color: gray;
    transition: all ease-in-out .1s;
}

.header__navbar>li>a:hover {
    background:rgb(240, 240, 240);
    color: black;
}

@media screen and (max-width: 1100px) {
    .header__navbar>li {
        margin: 0 10px;
    }
}

@media screen and (max-width: 850px) {
    .header__navbar>li>a {
        font-size: 12px;
    }
    .header__navbar>li {
        margin: 0;
    }
    .header__button {
        font-size: 10px;
        font-weight: 500;
        width: 100px;
        height: 30px;
    }
}

@media screen and (max-width: 650px) {
    .header__navbar, .header__button {
        display: none;
    }
}
.Home {
    background-image: url(/easybank/static/media/bg-intro-desktop.9cea2f98.svg);
    background-position: 47vw bottom;
    background-repeat: no-repeat;
    height: 800px;
}
.home__inner {
    display: flex;
    justify-content: flex-end;
}
.home__left {
    margin: auto;
    justify-self: start;
    margin-left: 200px;
    margin-top: 300px;
}
.home__left__title {
    width: 600px;
}
.home__left__text {
    width: 31rem;
}
.home__left > * {
    margin-bottom: 40px;
}
.home__phones {
    position: absolute;
    margin: 0 0 0 180px;
}
@media screen and (max-width: 1300px) {
    .home__left {
        margin-left: 100px;
    }
    .home__left__title{
        font-size: 40px;
        width: 400px;
    }
    .home__left__text {
        font-size: 15px;
        width: 24rem;
    }
    .home__phones {
        width: 500px;
        margin: 180px 0 0 0;
    }
}
@media screen and (max-width: 1000px) {
    .Home {
        background-image: url(/easybank/static/media/bg-intro-mobile.c23dd1d0.svg);
        background-position: left -28rem;
        background-size: 100% auto;
        height: 700px;
    }
    .home__inner {
        justify-content: center;
    }
    .home__left {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0;
        margin-top: 400px;
    }
    .home__phones {
        width: 300px;
        margin: 0 0 0 0;
    }
    .home__left__title{
        font-size: 40px;
        width: 400px;
    }
    .home__left__text {
        width: 20rem;
    }
    .home__left > * {
        text-align: center;
        margin-bottom: 20px;
    }
}
@media screen and (max-width: 800px) {
    .Home {
        background-position: left -18rem;
    }
}
@media screen and (max-width: 500px) {
    .Home {
        background-position: left -5rem;
    }
    .home__phones {
        width: 300px;
    }
    .home__left__title{
        font-size: 30px;
        width: 300px;
    }
    .home__left__text {
        font-size: 12px;
        width: 18rem;
    }
}
.Features {
    padding: 6em 0;
    background: rgb(240, 240, 240);
}
.features__top {
    margin: 0 200px;
}
.features__top__title {
    margin-bottom: 20px;
    font-size: 2.8em;
}
.features__top__text {
    margin-bottom: 80px;
    width: 45rem;
}
.feature__box {
    margin: 0 100px 0 200px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
.feature {
    position: relative;
    margin: 10px;
    display: grid;
    grid-row-gap: 1.3em;
    row-gap: 1.3em;
    bottom: 0px;
    padding: 2em .88em;
    max-width: 22rem;
    background: rgba(0, 0, 0, 0);
    transition: ease-in-out .1s;
}
.feature:hover {
    bottom: 10px;
    background: rgb(233, 233, 233);
    border-radius: 5px;
}
.feature__title {
    font-size: 20px;
}
@media screen and (max-width: 1300px) {
    .features__top, .feature__box {
        margin: 0 100px;
    }
    .features__top__title {
        font-size: 40px;
    }
    .features__inner p {
        font-size: 15px;
    }
    .features__top__text {
        width: 35rem;
    }
    .feature__box {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media screen and (max-width: 1000px) {
    .Features {
        margin: 0;
        text-align: center;
    }
    .features__top, .feature__box, .feature {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0;
    }
    .features__top__text, .feature__paragraph {
        max-width: 18rem;
    }
}
@media screen and (max-width: 500px) {
    .features__top__title {
        font-size: 30px;
        margin: 0px 10px 20px;
    }
    .features__inner p {
        font-size: 12px;
    }
}
.Articles {
    padding: 6em 0;
    background: rgb(248, 248, 248);
}
.articles__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.articles__inner__title {
    align-self: flex-start;
    margin: 0 200px 60px;
    font-size: 2.8em;
}
.articles__inner__box {
    display: grid;
    grid-row-gap: 5rem;
    row-gap: 5rem;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    grid-template-columns: repeat(4,1fr);
    margin: 0 50px 0 200px;
    max-width: 110em;
}
.articles__inner__box .article {
    display: grid;
    position: relative;
    bottom: 0;
    border-radius: 5px;
    grid-template-rows: 15rem;
    border-radius: 10px;
    background-color: white;
    transition: ease-in-out .1s;
}
.articles__inner__box .article:hover {
    bottom: 10px;
    color: yellowgreen;
    background-color: rgb(240, 240, 240);
}
.articles__inner__box .article__image {
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    height: 100%;
}
.article__txt {
    padding: 1.7em 1.3em;
    display: grid;
    grid-row-gap: .5em;
    row-gap: .5em;
}
.articles__inner__box .article__txt__author {
    font-size: 12px;
    color: #9698a6;
    font-weight: 400;
}
.articles__inner__box .article__txt__title {
    font-size: 18px;
}
.articles__inner__box .article__txt__paragraph {
    font-size: 14px;
}
@media screen and (max-width: 1300px) {
    .articles__inner__title {
        margin: 0 100px 60px;
        font-size: 40px;
    }
    .articles__inner__box {
        grid-template-columns: repeat(2,1fr);
    }
    .articles__inner__box {
        max-width: 40em;
        margin: 0 50px 0 100px;
    }
    .articles__inner__box .article {
        grid-template-rows: 10rem;
    }
}
@media screen and (max-width: 1000px) {
    .articles__inner__title {
        align-self: center;
        margin: 0 0 60px;
    }
    .articles__inner__box {
        margin: 0 20px;
    }
}
@media screen and (max-width: 500px) {
    .articles__inner__title {
        font-size: 30px;
    }
    .articles__inner__box {
        grid-template-columns: repeat(1,1fr);
    }
    .articles__inner__box {
        margin: 0 20px;
        max-width: 15em;
    }
    .articles__inner__box .article__txt__paragraph {
        font-size: 12px;
    }
}
.Footer {
    background: #2d314d;
    padding: 4em 0;
}
.footer__inner {
    display: flex;
    justify-content: space-around;
}
.footer__top__social{
    display: flex;
    justify-content: center;
    margin: 40px 0 0 0;
}
.footer__top__social>img {
    margin: 0 5px;
    cursor: pointer;
    transition: ease-in-out .1s;
}
.footer__middle {
    display: flex;
}
.footer__middle_left>li, .footer__middle_right>li {
    cursor: pointer;
    margin: 10px 50px;
    color: white;
    transition: ease-in-out .1s;
}
.footer__middle_left>li:hover, .footer__middle_right>li:hover {
    color: yellowgreen;
}
.footer__bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.footer__bottom>button, .footer__bottom>p {
    margin: 0 0 10px 0;
}
.footer__bottom>p {
    font-size: 14px;
}
.footer__bottom>p>a.link {
    cursor: pointer;
    color: rgb(71, 122, 218);
}
@media screen and (max-width: 1000px) {
    .footer__middle_left>li, .footer__middle_right>li {
        font-size: 12px;
        margin: 10px 10px;
    }
    .footer__bottom>p {
        font-size: 10px;
    }
}
@media screen and (max-width: 650px) {
    .footer__inner {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .footer__middle_left>li, .footer__middle_right>li {
        margin: 10px;
        text-align: center;
    }
    .footer__bottom {
        margin: 10px;
        align-items: center;
    }
}
@media screen and (max-width: 500px) {
    .footer__middle {
        flex-direction: column;
    }
}

